import React from "react";
import { Button } from 'antd';

export default ({
  children = "",
  label = "",
  className = "",
  isActive = null,
  type = false,
  onClick = () => null
}) => {
  const _class = `${className} ${
    isActive ? "bg-primary border-primary" : "bg-lighter border-grey"
  } cursor-pointer  font-bold  hover:text-black text-dark px-4 py-1 rounded inline-flex items-center text-xs `;
  const child = label !== "" ? label : children;
  if (type === "submit") {
    return <input type="submit" value={child} className={_class} />;
  }

  return (
    <div onClick={onClick} className={_class}>
      {child}
    </div>
  );
};