import React from "react"

export default ({
    children = "",
    label = "",
    className = "",
}) => {
    return (
        <div className={`${className} lg:w-50por md:w-80por w-90por h-50vh pxy flex justify-center items-center flex-col text-center`}>
            {label !== "" ? label : children}
        </div>
    )
}
